.info {
    position: absolute;
    left: 100px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

@media (max-width:990px) {
    .info {
        left: 60px;
        right: 60px;
        top: auto;
        bottom: 60px;
        -webkit-transform: none;
            -ms-transform: none;
                transform: none;
    }
}


